import { RowData, assert, mergeOptions } from "./util/helper";

/**
 * @public
 */
export interface LookupParams {
  from: RowData[];
  key: string;
  field: string;
  values?: string[];
  as?: string[];
  default?: any;
}

/**
 * join
 * @param rows - 数据
 * @param options - 参数
 *
 * @public
 */
export function lookup(rows: RowData[], options: LookupParams): RowData[] {
  const opts = mergeOptions(options, { as: [], default: null });
  const { as, values, field, from, key } = opts;
  return rows.map(row => {
    const item = { ...row };
    const target = from.find(item => item[key] === row[field]);

    if (!values) {
      assert(
        Array.isArray(as) && as[0],
        `as is required when values is unspecified`
      );
      if (!target) item[as[0]] = opts.default;
      else item[as[0]] = target;
    } else {
      values.map((key, index) => {
        item[as[index] || key] = target ? target[key] : opts.default;
      });
    }
    return item;
  });
}
