import * as G2 from "@antv/g2";
import { ListItem } from "@antv/g2/lib/dependents";
import ListUnchecked from "@antv/g2/lib/interaction/action/component/list-unchecked";

// 图例单选 Action
class ListChecked extends ListUnchecked {
  toggle() {
    // 获取原始事件
    const originEvent = this.context.event.event;
    const { item, list } = this.getTriggerListInfo() || {};
    if (!item) {
      return;
    }
    // 是否按Control
    const hasControl = originEvent.ctrlKey || originEvent.metaKey;
    // 当前选中状态 false-选中，true-未选
    const currentEnable = this.hasState(list, item);
    if (hasControl) {
      // Control时反选
      this.setItemState(list, item, !currentEnable);
    } else {
      // 非Control时单选
      const listItems = list.getItems();
      const isOneChecked =
        listItems.reduce((pre: number, cur: ListItem) => {
          return pre + (this.hasState(list, cur) ? 0 : 1);
        }, 0) === 1;
      listItems.forEach((listItem: ListItem) => {
        const enable = this.hasState(list, listItem);
        const otherEnable = !currentEnable && isOneChecked ? !enable : true;
        if (listItem !== item) {
          this.setItemState(list, listItem, otherEnable);
        } else {
          this.setItemState(list, listItem, false);
        }
      });
    }
  }
}
// @ts-ignore
G2.registerAction("list-checked", ListChecked);

// 图例单选封装交互
G2.registerInteraction("legend-single-filter", {
  showEnable: [
    { trigger: "legend-item:mouseenter", action: "cursor:pointer" },
    { trigger: "legend-item:mouseleave", action: "cursor:default" }
  ],
  start: [
    { trigger: "legend-item:click", action: "list-checked:toggle" },
    { trigger: "legend-item:click", action: "data-filter:filter" }
  ]
});

G2.registerInteraction("locked-tooltip", {
  start: [
    {
      trigger: "plot:click",
      action: context => {
        const locked = context.view.isTooltipLocked();
        if (locked) {
          context.view.unlockTooltip();
          context.view.emit("tooltip:unlock");
        } else {
          const { x, y } = context.event;
          const data = context.view.getSnapRecords({ x, y });
          const xValue: number | undefined = data.length
            ? data[0]._origin.time
            : undefined;
          context.view.emit("tooltip:lock", xValue);
          context.view.lockTooltip();
        }
      }
    },
    { trigger: "plot:mousemove", action: "tooltip:show" }
  ],
  end: [{ trigger: "plot:mouseleave", action: "tooltip:hide" }]
});
