// @ts-nocheck
import { sumBy, orderBy } from 'lodash';

const partner = /\{\{(.+?)\}\}/g;

export function parse(source: string, ctx: Record<string, unknown>): string {
  partner.lastIndex = 0;
  return source.replace(partner, (_var1, var2) => {
    return (ctx as any)[var2];
  });
}

export function stringify(source: { [key: string]: any }): string {
  const { __ds__ = '', __name__, ...rest } = source;
  // 表格可能没有指标名称
  const prefix = [__ds__, __name__].filter((item) => !!item).join('/');
  if (Object.keys(rest).length === 0) return prefix;
  return `${prefix}{${Object.keys(rest).map((item) => `${item}="${source[item]}"`)}}`;
}

interface StringTypeData {
  type: string;
  time: number;
  value: number | null;
  labels: { [key: string]: string };
  meta: any;
}

export interface ObjectTypeData {
  labels: { [key: string]: string };
  legend?: string;
  meta?: any;
  data: Array<[number, number | null]>;
}

export function transform(series: any[]): ObjectTypeData[] {
  const result: ObjectTypeData[] = [];
  series.forEach(({ name, data, legend, meta }) => {
    data.forEach(({ labels, values }) => {
      result.push({
        labels: { __ds__: name, ...labels },
        legend,
        meta,
        data: values,
      });
    });
  });
  return result;
}

/**
 * 转化为 tabluar 数据源
 * @param source
 */
export function convert(source: ObjectTypeData[]): StringTypeData[] {
  const result: StringTypeData[] = [];
  source.forEach(({ data, labels, legend, meta }) => {
    const type = legend ? parse(legend, labels) : stringify(labels);
    data.forEach(([time, value]) => {
      result.push({ time, value, labels, type, meta });
    });
  });
  return result;
}

/**
 * 获取 Series 里的一个值
 * @param series - 数据
 * @param type - 值的类型
 */
export function getValue(series: Series[], type: 'current' | 'min' | 'max' | 'avg' | 'total' = 'current') {
  const source = transform(series);
  const values = source.map(({ data }) => {
    if (type === 'min') {
      return Math.min(...data.filter((item) => item[1] !== null).map((item) => item[1] as number));
    } else if (type === 'max') {
      return Math.max(...data.filter((item) => item[1] !== null).map((item) => item[1] as number));
    } else if (type === 'total') {
      const d = data.filter((item) => item[1] !== null);
      return sumBy(d, '1');
    } else if (type === 'avg') {
      const d = data.filter((item) => item[1] !== null);
      return sumBy(d, '1') / d.length;
    }
    const lastIndex = data.length - 1;
    return data[lastIndex][1];
  });

  return source.map((item, index) => {
    const { labels, legend } = item;
    return { type: legend ? parse(legend, labels) : stringify(labels), value: values[index] };
  });
}

export function sortTooltip(title: string, data: Array<{ color: string; name: string; value: number }>) {
  return `<div class="g2-tooltip">
  <div class="g2-tooltip-title">${title}</div>
  <ul class="g2-tooltip-list">
  ${orderBy(data, (item) => Number(item.value), 'desc')
    .map((item) => {
      return `<li class="g2-tooltip-list-item">
    <span class="g2-tooltip-marker" style="background: ${item.color}; width: 8px; height: 8px; border-radius: 50%; display: inline-block; margin-right: 8px;"></span>
    <span class="g2-tooltip-name">${item.name}</span>:<span class="g2-tooltip-value">${item.value}</span>
  </li>`;
    })
    .join('')}
  </ul>
</div>`;
}

export function intelligentRepair(num: number): number {
  if (!num) return 0;
  const str = `${num}`;
  const index = str.indexOf('.');
  if (index !== 1 && str[0] !== '0') return Number(num.toFixed(2));
  const a = str.substr(index + 1);
  let digits = 0;
  for (let i = 0; i < a.length; i++) {
    if (a[i] !== '0') {
      digits = i;
      break;
    }
  }
  return Number(num.toFixed(digits + 2));
}
