// @ts-nocheck
import * as React from 'react';
import * as G2 from '@antv/g2';
import numeral from 'numeraljs';
import moment from 'moment';
import classNames from 'classnames';
import { sortTooltip, intelligentRepair } from './utils';
import { Table, Slider, Tooltip, Popover } from 'antd';
import './g2Interaction';
import ClickOutside from 'react-outside-click-handler';
import { forIn, maxBy } from 'lodash';
import { aggregate } from './transforms/aggregate';
import { lookup } from './transforms/lookup';

export interface LineConfig {
  stack?: boolean;
  xAxis: {
    visible: boolean;
    label: {
      formatter: string;
    };
  };
  yAxis: {
    visible: boolean;
    min?: number;
    max?: number;
    label?: {
      formatter: string;
    };
  };
  lineStyle: {
    smooth: boolean;
    size: number;
  };
  point?: {
    visible: boolean;
    size: number;
  };
  legend: {
    visible: boolean;
    asTable: boolean;
    position: 'bottom' | 'right-top';
    values: {
      min: boolean;
      max: boolean;
      avg: boolean;
      total: boolean;
    };
  };
}

export interface LineProps {
  data: any;
  width: number;
  height: number;
  config: LineConfig;
  onClick?(e: any): void;
}

export interface LegendDomPrps {
  tableData: any;
  colors: any[];
}

export default function LineChart(props: LineProps) {
  const {
    data,
    width,
    height,
    config,
    // onClick,
    config: {
      xAxis,
      legend,
      yAxis,
      point,
      yAxis: { min, max },
      lineStyle: { smooth, size },
      stack,
    },
  } = props;
  const outerRef = React.useRef<HTMLDivElement>(null);
  const containerRef = React.useRef<HTMLDivElement>(null);
  const chartRef = React.useRef<G2.Chart>();
  const legendDomRef = React.useRef<HTMLDivElement>(null);

  const [selected, setSelected] = React.useState<string>('');
  const [colors, setColors] = React.useState<any>([]);
  const [lockedTime, setLockedTime] = React.useState<undefined | number>();
  const [padding, setPadding] = React.useState<number[]>([0, 0, 0, 0]);
  const [yScaleRange, setYScaleRange] = React.useState<[number, number]>([0, 0]);
  const [UDRange, setUDRange] = React.useState<[number, number]>([0, 0]);
  const [active, setActive] = React.useState<boolean>(false);
  const [pinX, setPinX] = React.useState<number>(-100);
  // 将显示值转换为表格数据格式
  const dataInfo = React.useMemo(() => {
    return aggregate(data, {
      as: ['min', 'max', 'total', 'avg'],
      fields: ['value', 'value', 'value', 'value'],
      groupBy: 'type',
      op: ['min', 'max', 'sum', 'average'],
    });
  }, [data]);

  const maxTime = React.useMemo(() => {
    const target = maxBy(data, (item: any) => item.time);
    return target ? target.time : undefined;
  }, [data]);

  const tableDataSource = React.useMemo(() => {
    const targetData = data.filter((item: any) => item.time === (lockedTime || maxTime));
    return lookup(dataInfo, {
      from: targetData,
      key: 'type',
      field: 'type',
      values: ['value'],
      as: ['current'],
    });
  }, [data, lockedTime || maxTime]);

  const legendClick = (record: any) => {
    let newSelected = selected;
    if (newSelected !== record.type) {
      newSelected = record.type;
    } else {
      newSelected = '';
    }
    setSelected(newSelected);
  };

  // 表格图例
  function LegendDom(props: LegendDomPrps) {
    const { tableData, colors } = props;
    const columns: any[] = [
      {
        title: '指标',
        dataIndex: 'type',
        key: 'type',
        render: (text: string, record: any, i: number) => {
          const selectedStyle = {
            color: '#999',
            backgroundColor: '#999',
          };
          if (selected.length === 0 || selected === record.type) {
            selectedStyle.color = 'rgba(0, 0, 0, 0.65)';
            selectedStyle.backgroundColor = colors[i];
          }
          return (
            <div>
              <span style={{ display: 'inline-block', width: 10, height: 2, verticalAlign: 'middle', marginRight: 5, ...selectedStyle }} />
              <span>{text}</span>
            </div>
          );
        },
      },
    ];
    forIn(legend.values, (value, key) => {
      if (!value) return;
      if (key === 'min') {
        columns.push({
          title: '最小值',
          dataIndex: 'min',
          key: 'min',
          align: 'right',
          sorter: (rowA: any, rowB: any) => rowA.min - rowB.min,
        });
      } else if (key === 'max') {
        columns.push({
          title: '最大值',
          dataIndex: 'max',
          key: 'max',
          align: 'right',
          sorter: (rowA: any, rowB: any) => rowA.max - rowB.max,
        });
      } else if (key === 'avg') {
        columns.push({
          title: '平均值',
          dataIndex: 'avg',
          key: 'avg',
          align: 'right',
          sorter: (rowA: any, rowB: any) => rowA.avg - rowB.avg,
          render(num: number) {
            return intelligentRepair(num);
          },
        });
      } else if (key === 'total') {
        columns.push({
          title: '总和',
          dataIndex: 'total',
          key: 'total',
          align: 'right',
          sorter: (rowA: any, rowB: any) => rowA.total - rowB.total,
        });
      }
    });
    columns.push({
      title: moment(lockedTime ? lockedTime : maxTime).format(xAxis.label.formatter),
      dataIndex: 'current',
      key: 'current',
      align: 'right',
      sorter: (rowA: any, rowB: any) => rowA.current - rowB.current,
    });
    return (
      <div
        ref={legendDomRef}
        style={{ marginLeft: 20, width: 'auto', height, overflow: 'auto' }}
        className={legend.position === 'bottom' ? 'magi-plugin-legend-table-bottom' : 'magi-plugin-legend-table-right'}
      >
        <Table
          tableLayout="auto"
          rowKey="type"
          className="chart-legend-table"
          onRow={(record: any) => ({
            onClick: () => {
              legendClick(record);
            },
          })}
          pagination={false}
          size="small"
          dataSource={tableData}
          columns={columns}
        />
      </div>
    );
  }

  function render(chart: G2.Chart, range?: [number, number]) {
    chart.clear();
    chart.changeData(data);
    chart.filter('type', (type: string) => (selected === '' ? true : selected === type));
    chart
      .scale('time', { type: 'time', mask: xAxis.label.formatter })
      .scale('value', {
        min: range && range[0] ? range[0] : min,
        max: range && range[1] ? range[1] : max,
        nice: min === undefined && max === undefined,
        formatter(value: number) {
          if (yAxis.label && yAxis.label.formatter === 'percent') {
            return numeral(value).format('0.0%');
          }
          return value;
        },
      })
      .tooltip({
        shared: true,
        showCrosshairs: true,
        customContent: sortTooltip,
      });
    chart.axis('time', xAxis.visible).axis(yAxis.visible);

    if (legend && legend.visible && !legend.asTable) {
      chart.legend({
        position: legend.position,
        flipPage: true,
        itemName: {
          formatter: (text: string) => {
            let result = '';
            for (const key in legend.values) {
              const legendValue = data.legendValues[key];
              if (legendValue) {
                legendValue.forEach((el: any) => {
                  if (el.type === text) {
                    if (key === 'min') {
                      result += ` 最小值: ${el.value}`;
                    } else if (key === 'max') {
                      result += ` 最大值: ${el.value}`;
                    }
                    if (key === 'avg') {
                      result += ` 平均值: ${el.value}`;
                    }
                    if (key === 'total') {
                      result += ` 总和: ${el.value}`;
                    }
                  }
                });
              }
            }
            return `${text} ${result}`;
          },
        },
      });
    } else {
      chart.legend(false);
    }

    const line = chart
      .line({ connectNulls: false })
      .size(size)
      .position('time*value')
      .color('type')
      .shape(smooth ? 'smooth' : 'line');
    if (stack) {
      line.adjust('stack');
      chart
        .area({ connectNulls: false })
        .position('time*value')
        .color('type')
        .shape(smooth ? 'smooth' : 'line')
        .adjust('stack');
    }

    if (point && point.visible) {
      const size: number = point.size ? point.size : 2;
      const pointGeom = chart.point().position('time*value').color('type').size(size);
      if (stack) {
        pointGeom.adjust('stack');
      }
    }
    chart.render();
  }

  React.useEffect(() => {
    const chart = chartRef.current;
    if (!chart) return;
    let chartWidth = width,
      chartHeight = height;
    if (legend.position === 'bottom') {
      chartHeight = height - (legendDomRef.current?.clientHeight ? legendDomRef.current?.clientHeight + 20 : 0);
    } else if (legend.position === 'right-top') {
      chartWidth = width - (legendDomRef.current?.clientWidth ? legendDomRef.current?.clientWidth + 20 : 0);
    }
    chart.changeSize(chartWidth, chartHeight);
    render(chart, UDRange);
    setPadding(chart.autoPadding!);
  }, [config, selected]);

  React.useEffect(() => {
    const chart = chartRef.current;
    if (!chart) return;
    let chartWidth = width,
      chartHeight = height;
    if (legend.position === 'bottom') {
      chartHeight = height - (legendDomRef.current?.clientHeight ? legendDomRef.current?.clientHeight + 20 : 0);
    } else if (legend.position === 'right-top') {
      chartWidth = width - (legendDomRef.current?.clientWidth ? legendDomRef.current?.clientWidth + 20 : 0);
    }
    chart.changeSize(chartWidth, chartHeight);
    setPadding(chart.autoPadding);
  }, [width, height]);

  React.useEffect(() => {
    const chart = chartRef.current;
    if (!chart) return;
    chart.changeData(data);
  }, [data]);

  React.useEffect(() => {
    const chart = chartRef.current;
    if (!chart) return;
    render(chart, UDRange);
  }, [UDRange[0], UDRange[1]]);

  React.useEffect(() => {
    const container = containerRef.current;
    if (container) {
      let chartWidth = width,
        chartHeight = height;
      if (legend.position === 'bottom') {
        chartHeight = height - (legendDomRef.current?.clientHeight ? legendDomRef.current?.clientHeight + 20 : 0);
      } else if (legend.position === 'right-top') {
        chartWidth = width - (legendDomRef.current?.clientWidth ? legendDomRef.current?.clientWidth + 20 : 0);
      }
      const chart = new G2.Chart({
        container: container,
        width: chartWidth,
        height: chartHeight,
        limitInPlot: true,
      });
      setColors(dataInfo.length <= 10 ? chart.getTheme().colors10 : chart.getTheme().colors20);
      render(chart);
      setPadding(chart.autoPadding);
      const { min, max } = chart.geometries[0].getYScale();
      setYScaleRange([min, max]);
      setUDRange([min, max]);
      chartRef.current = chart;
      chart.removeInteraction('legend-filter');
      chart.interaction('legend-single-filter');
      chart.removeInteraction('tooltip');
      chart.interaction('locked-tooltip');
      // chart.on('click', (event: any) => {
      //   onClick(event);
      // });
      chart.on('tooltip:lock', (time: number) => {
        setLockedTime(time);
      });
      chart.on('tooltip:unlock', () => {
        setLockedTime(undefined);
      });
      chart.on('tooltip:change', (e: any) => {
        const { x } = e;
        setPinX(x);
      });
      chart.on('tooltip:hide', () => {
        setPinX(-100);
      });
    }
    return () => {
      if (chartRef.current) chartRef.current.destroy();
    };
  }, []);
  const [s, e] = yScaleRange;
  const [top, , bottom, left] = padding;
  const sliderHeigth = height - top - bottom;
  return (
    <ClickOutside
      onOutsideClick={() => {
        setActive(false);
      }}
    >
      <div ref={outerRef} style={{ position: 'relative' }}>
        <div ref={containerRef} />
        {active ? (
          <div className="chart-y-slider-container active" style={{ width: left, height: sliderHeigth + 10, top: 0 }}>
            {dataInfo.map((item) => {
              return (
                <div
                  key={item.type}
                  style={{
                    position: 'absolute',
                    top: sliderHeigth - (sliderHeigth * item.max) / e,
                    width: 6,
                    height: 6,
                    right: 11,
                    background: 'rgba(140, 140, 140, 0.85)',
                    borderRadius: 6,
                  }}
                />
              );
            })}
            <Popover
              getPopupContainer={() => {
                return outerRef.current!;
              }}
              defaultVisible={true}
              content={
                <div
                  style={{ cursor: 'pointer' }}
                  onClick={(e) => {
                    e.stopPropagation();
                    setActive(false);
                    setUDRange(yScaleRange);
                  }}
                >
                  重置
                </div>
              }
            >
              <div className="ant-slider-container">
                <Slider
                  vertical
                  range
                  value={UDRange}
                  min={s}
                  max={e}
                  onChange={(value) => {
                    setUDRange(value);
                  }}
                />
              </div>
            </Popover>
          </div>
        ) : (
          <Tooltip title="Y轴范围控制">
            <div
              className={classNames('chart-y-slider-container', { active })}
              onClick={() => {
                setActive(true);
              }}
              style={{ width: left, height: sliderHeigth + 10, top: 0 }}
            >
              <div className="corner-tip tl" />
              <div className="corner-tip tr" />
              <div className="corner-tip bl" />
              <div className="corner-tip br" />
            </div>
          </Tooltip>
        )}
        <img style={{ position: 'absolute', left: pinX, top: -16 }} src="https://gw.alipayobjects.com/zos/bmw-prod/066d58cc-1466-4aca-8421-b7ec919aa620.svg" />
        {legend.visible && legend.asTable ? <LegendDom tableData={tableDataSource} colors={colors} /> : null}
      </div>
    </ClickOutside>
  );
}
